import React, { Component } from 'react';
import spa from './../../../images/S-logo1.png';
import service1 from './image/services/services1.jpg';
import service2 from './image/services/services2.jpg';
import service3 from './image/services/services3.jpg';
import './css/agency.css';
import './fonts/font-awesome-4.7.0/css/font-awesome.css';


import { Link } from "react-router-dom";

class New extends Component {
    constructor(props) {
        super(props);
        this.state = {
            scrolling: false,
            services: false,
            portfolio: false,
            team: false,
            contact: false,
            width: null,
            
        };
    }
    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }
     
    handleScroll = (event) => {
        let scrollTop1 = event.target.body.scrollTop;
        let scrollTop2 = event.target.documentElement.scrollTop;
        let scrollTop = Math.max(scrollTop1, scrollTop2);
        let  width = this.props.width;
       
        
        if(width >=992){
            if (scrollTop > 100) {
                this.setState({
                    scrolling: true,
                });
            }
            else {
                this.setState({
                    scrolling: false,
    
                });
            }
            if (scrollTop < 645) {
                this.setState({
                    services: false,
                    portfolio: false,
                    team: false,
                    contact: false
                });
            }
            else if (scrollTop < 1442) {
                this.setState({
                    services: true,
                    portfolio: false,
                    team: false,
                    contact: false
                });
            }
            else if (scrollTop < 2544) {
                this.setState({
                    services: false,
                    portfolio: true,
                    team: false,
                    contact: false
                });
            }
            else if (scrollTop < 3960) {
                this.setState({
                    services: false,
                    portfolio: false,
                    team: true,
                    contact: false
                });
            }
            else if (scrollTop >= 3960) {
                this.setState({
                    services: false,
                    portfolio: false,
                    team: false,
                    contact: true
                });
            }
        }
        else {
            this.setState({
                services: false,
                portfolio: false,
                team: false,
                contact: false
            });
        }
        

    }
    render() {
        return (
            <div>
                <nav className={this.state.scrolling ? 'navbar navbar-expand-lg navbar-dark fixed-top navbar-shrink' : 'navbar navbar-expand-lg navbar-dark fixed-top'} id="mainNav">
                    <div className="container row m-t-10" >
                        <div class="col-xs-1 col-sm-1 col-md-1 col-lg-1"> </div>
                        <div className="logohome col-md-4 ">
                            <a href="/">
                                <img src={spa} alt="spa" className="img-ficon" />
                            </a>
                        </div>
                        <button className="navbar-toggler navbar-toggler-right  " type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                            Menu
                            <i className="fa fa-bars m-l-4"></i>
                        </button>
                        <div class="col-xs-7 col-sm-7 col-md-7 col-lg-7 "> </div>
                        <div className="collapse navbar-collapse col-md-2" id="navbarResponsive">
                            {/* <ul className="navbar-nav text-uppercase ml-auto">
                                <li className="nav-item">
                                    <a className={this.state.services ? "nav-link js-scroll-trigger active nav-link-center oneline" : "nav-link js-scroll-trigger nav-link-center oneline"} href="#services" >Dch V</a>
                                </li>
                                <li className="nav-item">
                                    <a className={this.state.portfolio ? "nav-link js-scroll-trigger active nav-link-center" : "nav-link js-scroll-trigger nav-link-center"} href="#portfolio" >Danh mc ca hng</a>
                                </li>
                                <li className="nav-item">
                                    <a className={this.state.team ? "nav-link js-scroll-trigger active nav-link-center" : "nav-link js-scroll-trigger nav-link-center"} href="#team" >i pht trin</a>
                                </li>
                                <li className="nav-item">
                                    <a className={this.state.contact ? "nav-link js-scroll-trigger active  oneline" : "nav-link js-scroll-trigger oneline"} href="#contact">Lin h</a>
                                </li>
                            </ul> */}
                            <div className="menu-buttons ">
                                <Link className="btn btn-outline" to="/login" onClick={this.props.isLoGin}>ng nhp</Link>
                            </div>
                        </div>

                    </div>
                </nav>

                <header className="masthead">
                    <div className="container">
                        <div className="intro-text">
                            <div className="intro-lead-in">Cho mng n vi Shopee Price Analytics</div>
                            <div className="intro-heading text-uppercase">Knh so snh v iu chnh gi i th trn sn TMT Shopee</div>
                            <a className="btn btn-primary btn-xl text-uppercase js-scroll-trigger" href="#services">Xem thm</a>
                        </div>
                    </div>
                </header>

                <section className="page-section" id="services">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12 text-center">
                                <h2 className="section-heading text-uppercase">Dch V</h2>
                                <h3 className="section-subheading text-muted">
                                    <div>SPA  Shopee Price Asisstance c xy dng vi mc tiu cui cng l gip cc ch ca hng bn hng trn sn TMT Shopee </div>
                                  c ci nhn tng th v cc sn phm tng t sn phm trong shop ang cng c rao bn trn sn.</h3>
                            </div>
                        </div>
                        <div className="row text-center">
                            <div className="col-md-4">
                                <img className="img-services" src={service1} alt="" />
                                <h4 className="service-heading">Qun l bn hng</h4>
                                <p className=" text-muteded ">Qun l hng ha khng gii hn. D dng tm kim thng tin sn phm. Cp nhp thng tin hng ha tc thi mi lc mi ni</p>
                            </div>
                            <div className="col-md-4">
                                <img className="img-services" src={service2} alt="" />
                                <h4 className="service-heading">Lm ch th trng</h4>
                                <p className="text-muteded">Theo di st din bin gi c th trng. Tnh ton mc  v thi im iu chnh ph hp gip kim kim sot mt bng gi chung</p>
                            </div>
                            <div className="col-md-4">
                                <img className="img-services" src={service3} alt="" />
                                <h4 className="service-heading">iu chnh gi i th</h4>
                                <p className="text-muteded">Xc nh v theo di bin ng gi ca i th trn sn TMT Shopee. Cho php iu chnh gi ca sn phm cho ph hp nng cao sc cnh tranh</p>
                            </div>
                        </div>
                    </div>
                </section>
                <footer className="footer">
                    <div className="container">
                        <div className="row align-items-center">
                            <div className="col-md-4">
                                <span className="copyright">Copyright &copy; Your Website 2019</span>
                            </div>
                            <div className="col-md-4">
                                <ul className="list-inline social-buttons">
                                    <li className="list-inline-item" >
                                        <a href="/" id="fb">
                                            <i className="fa fa-facebook" aria-hidden="true"></i>
                                        </a>
                                    </li>
                                    <li className="list-inline-item" id="gm">
                                        <a href="/">
                                            <i className="fa fa-google" aria-hidden="true"></i>
                                        </a>
                                    </li>
                                    <li className="list-inline-item" id="tw">
                                        <a href="/">
                                            <i className="fa fa-twitter" aria-hidden="true"></i>
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <div className="col-md-4">
                                <ul className="list-inline quicklinks">
                                    <li className="list-inline-item">
                                        <a className=" text-white"href="/">Privacy Policy</a>
                                    </li>
                                    <li className="list-inline-item">
                                        <a className=" text-white" href="/">Terms of Use</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </footer>
            </div>
        );
    }
}

export default New;